/*
 * Login
 */
.bsw-body-login .ant-spin-container {
  padding: 0;
}
.bsw-body-login .login-box {
  height: 510px;
  margin: 0 auto;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  box-shadow: 0 0 10px #ddd;
}
.bsw-body-login .login-box .login-form {
  margin-top: 30px;
}
.bsw-body-login .login-box .left {
  width: 400px;
  height: 510px;
  background: url(/img/login.jpeg) center 0 no-repeat;
  background-size: cover;
  opacity: .9;
}
.bsw-body-login .login-box .left .logo {
  margin: 30px 0 0 30px;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.4);
  text-align: center;
  line-height: 40px;
  border-radius: 6px;
}
.bsw-body-login .login-box .left .logo img {
  height: 30px;
}
.bsw-body-login .login-box .left .login-img {
  text-align: center;
}
.bsw-body-login .login-box .left .login-img img {
  width: 300px;
  margin: 0 auto;
}
.bsw-body-login .login-box .right {
  background: #ffffff;
  width: 400px;
  height: 100%;
  padding: 84px 58px 0;
}
.bsw-body-login .login-box .right h2 {
  font-size: 20px;
  font-weight: 400;
  color: rgba(39, 38, 40, 0.75);
  margin-bottom: 20px;
  text-align: center;
}
.bsw-body-login .bsw-login-container .ant-input-lg,
.bsw-body-login .bsw-login-container .ant-btn-lg,
.bsw-body-login .bsw-login-container .bsw-captcha-item .ant-form-item-children > img.bsw-captcha {
  height: 44px;
  border-radius: 22px;
}
/*
 * Acme
 */
.ant-layout-sider .logo {
  margin: 30px 0 10px;
}
.ant-layout-sider .logo img {
  height: 40px;
}
.ant-layout-sider .logo,
.ant-layout-sider .ant-layout-header .logo {
  background: unset;
  box-shadow: unset;
}
.ant-layout-sider .logo.menu-collapse {
  width: 60px;
}
@media screen and (max-width: 992px) {
  .bsw-operate.top {
    margin-top: 0;
  }
  .bsw-body-login .login-box .left {
    width: 0;
  }
  .bsw-body-login .login-box .right {
    width: 360px;
    height: 100%;
    padding: 84px 16px;
  }
}
.ant-layout .ant-layout-header {
